import React from 'react';
import { previousTrainingPrograms } from '@/data/previousTrainingPrograms';
import Link from 'next/link';

export default function Previoustraining() {
  // Only show the first 3 previous programs on the home page
  const displayedPrograms = previousTrainingPrograms.slice(0, 3);

  return (
    <div id="previoustraining" className="py-12 bg-gray-50">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl font-bold text-[#004281] mb-8 text-center">Previous Training Programs</h2>
        
        <div className="grid sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {displayedPrograms.map((program, index) => (
            <div key={index} className="p-5 rounded-lg border border-gray-300 shadow-sm hover:shadow-md transition-shadow">
              <h3 className="text-lg font-bold text-[#004281] mb-3">{program.title}</h3>
              
              {program.location && (
                <div className="mb-2">
                  <p className="text-sm text-gray-600"><span className="font-semibold">Location:</span> {program.location}</p>
                </div>
              )}
              
              {program.date && (
                <div className="mb-2">
                  <p className="text-sm text-gray-600"><span className="font-semibold">Date:</span> {program.date}</p>
                </div>
              )}
              
              {program.duration && (
                <div className="mb-2">
                  <p className="text-sm text-gray-600"><span className="font-semibold">Duration:</span> {program.duration}</p>
                </div>
              )}
              
              {program.targetAudience && (
                <div className="mb-3">
                  <p className="text-sm text-gray-600"><span className="font-semibold">Target Audience:</span> {program.targetAudience}</p>
                </div>
              )}
              
              {program.brochureUrl && (
                <div className="mt-3">
                  <a 
                    href={program.brochureUrl}
                    className="inline-block bg-[#004281] hover:bg-[#003366] text-white text-sm font-medium py-1.5 px-3 rounded-full transition-colors"
                    download="Brochure"
                  >
                    Download Brochure
                  </a>
                </div>
              )}
            </div>
          ))}
        </div>
        
        <div className="mt-8 text-center">
          <Link 
            href="/archive" 
            className="inline-block bg-[#004281] hover:bg-[#003366] text-white font-medium py-2 px-6 rounded-full transition-colors"
          >
            View All Previous Programs
          </Link>
        </div>
      </div>
    </div>
  );
}
